span')]),_c('p',[_c('span',[_vm._v("1")]),_c('span',[_vm._v("、品类从边缘产品往核心部件转型")])]),_c('p',[_c('span')]),_c('p',[_c('span',[_vm._v("由于跨境物流的掣肘，在汽摩配品类上线跨境渠道初期，多是通过平邮、直邮卖一些轻小件的产品，如汽车的附件、装饰品。而汽车的核心零部件因为重量大和时效性的要求，却很难通过跨境渠道直邮到海外，毕竟汽车核心部件损坏，没有人愿意等待十几天的周期，购买配件从中国发货过去。")])]),_c('p',[_c('span')]),_c('p',[_c('span',[_vm._v("要想充分的发挥汽摩配品类的优势，必须把物流问题解决了。而海外仓的运营极大的拓展了汽配品类，现在eBay上成交的大部分汽摩配产品，基本都是海外仓完成的。像轮胎、减震器、刹车盘片，这些汽摩配品类中最核心的售后零部件，都是今年增长非常快的细分品类。")])]),_c('p',[_c('span')]),_c('p',[_c('span',[_vm._v("物流问题的解决，专业卖家也开始大量进入跨境电商行业，而这也是汽摩配品类往核心部件转移的重要原因。")])]),_c('p',[_c('span')]),_c('p',[_c('span',[_vm._v("在跨境渠道购买汽摩配产品的买家都是非常专业的，一类是个人买家，多是汽车发烧友，热衷于汽车DIY，他们喜欢自己动手修车也懂车；另一类是线下的汽车维修厂，他们的专业度也毋庸置疑。只有更专业的汽摩配卖家才能够满足这些专业客户的需求，而更核心的零部件也需要卖家拥有更为专业的知识。")])]),_c('p',[_c('span')]),_c('p',[_c('span',[_vm._v("经常会有客户问各种各样的问题。“我这个东西怎么会这样？”“这个东西要怎么调试”等等，卖家都要一一的去解答。比如，客户需要刹车盘片，肯定不能随便就下单购买，每一个汽车零部件都有其应用适配性，产品和型号都是一一对应的。")])]),_c('p',[_c('span')]),_c('p',[_c('span',[_vm._v("2")]),_c('span',[_vm._v("、市场呈现多元化的趋势")])]),_c('p',[_c('span')]),_c('p',[_c('span',[_vm._v("汽摩配售后市场和汽车保有量是密切相关的。正因如此，多年来欧美一直是全球最大的汽摩配售后市场，因为全球绝大部分的汽车都还是在这个地区的。但近年来，一些发展中国家包括一带一路沿线国家如俄罗斯、巴西等，汽车保有量的增长，也带动了这些地区汽摩配售后市场的扩大。")])]),_c('p',[_c('span')]),_c('p',[_c('span',[_vm._v("其实，随着全球汽摩配售后市场的逐年增长，加之更多的专业卖家进入跨境电商行业，越来越多的汽配卖家把拓展更多品类和品牌化的建设，列入战略布局之中，这也成为行业的新趋势。而品牌化的建设，对于当前阶段对于汽摩配卖家来说，不仅是历史的大机遇，也是行业发展的必然。以前汽摩配传统外贸工厂只能做贴牌做代工，自己的品牌却难以进入国外市场，而有了跨境电商这样的平台出现，卖家可以把自己的信息、自己的品牌直接展现给终端的消费者面前。这是一个历史机遇，庆幸的是很多卖家也已经意识到品牌化的重要性；")])]),_c('p',[_c('span')]),_c('p',[_c('span',[_vm._v("同时，随着行业的竞争加剧，消费者的要求也越来越高，以前可能白牌产品就可以销售的很好，现在却难以行得通——你的价格为什么没有差异？你的质量如何来体现？这些都需要品牌来背书，因此，品牌化也成也一个必然的趋势。")])]),_c('p',[_c('br')])])}]

